import { SessionProfiler } from '../src/engine/SessionProfiler';
import { OHLCV } from '../src/types/index';

describe('Debug SessionProfiler', () => {
  it('should debug storeAsianRange', () => {
    const profiler = new SessionProfiler();
    
    const testTimestamp = new Date('2024-01-01T02:00:00Z').getTime();
    console.log('Test timestamp:', testTimestamp);
    console.log('Test date:', new Date(testTimestamp));
    console.log('UTC hour:', new Date(testTimestamp).getUTCHours());
    
    const asianData: OHLCV[] = [{
      timestamp: testTimestamp,
      open: 50000,
      high: 50100,
      low: 49900,
      close: 50000,
      volume: 1000
    }];
    
    console.log('Before storeAsianRange, asianRange:', profiler.getAsianRange());
    profiler.storeAsianRange(asianData);
    console.log('After storeAsianRange, asianRange:', profiler.getAsianRange());
    
    expect(profiler.getAsianRange()).not.toBeNull();
  });
});